import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Radio, Row, Col, Alert } from "antd";

//const { Option } = Select;
//const { TextArea } = Input;

class ReturnReasonUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { returnReason } = this.props;
    const { loading } = returnReason;
    const data = returnReason.return_reason ? returnReason.return_reason : [];
    const used = data.is_used;
    const error = returnReason.errorField ? returnReason.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = { 
      display: "flex", 
      width: "100%" 
    }

    const radioButtonStyle = { 
      flex: "1", 
      textAlign: "center" 
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="returnReason.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="returnReasons.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="returnReasons.add.code" />}
              validateStatus={
                error.return_reason_code || getFieldError("return_reason_code")
                  ? "error"
                  : null
              }
              help={
                error.return_reason_code
                  ? error.return_reason_code
                  : getFieldError("return_reason_code")
              }
            >
              {getFieldDecorator("return_reason_code", {
                initialValue: data.return_reason_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={used ? true : false} />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="transactionType" />}
            >
              {getFieldDecorator("transaction_type", {
                initialValue: data.transaction_type,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group 
                  buttonStyle="solid" 
                  disabled={used ? true : false}
                  style={radioGroupStyle}
                >
                  <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="All" /></Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={2}><IntlMessages id="Purchase" /></Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={3}><IntlMessages id="Sales" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
          </div>
        </div>
  
        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
            disabled={used ? true : false}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedReturnReasonUpdateForm = Form.create({ name: "update_return_reason" })(ReturnReasonUpdateForm);

export default WrappedReturnReasonUpdateForm;
