import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import ReturnReasonUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/returnReason/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/return-reasons";

class UpdateReturnReason extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editReturnReason } = this.props;
    const { id } = this.props.match.params;
    editReturnReason(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.returnReason.error) {
      notification("error", this.props.returnReason.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.returnReason.success) {
      this.props.history.replace(redirectTo);
    }
  }

  handleSubmit = value => {
    const { updateReturnReason } = this.props;
    const { id } = this.props.match.params;
    updateReturnReason(id, value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="returnReasons.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.returnReason.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <ReturnReasonUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                returnReason={this.props.returnReason}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    returnReason: state.returnReason
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateReturnReason);
